import { IFrameReactContainer, useProxyWatchUpdates } from "@fanfanlo";
import { IAndroidWatchFile } from "@market/lib";
import { Box, Stack } from "@mui/material";
import { marketHtmlUtils } from "@src/markets/market-html-utils";
import { marketsDevScriptLib } from "@src/markets/markets-script-dev-lib";

function createHtml(script:string){
    // return marketHtmlUtils.createHtml(script);
    return `<html>
    <head>
        <title>script</title>
    </head>
    <body>
        <div id="app"></div>
        <script>${script}</script>
    </body>
</html>`
}
function Script({resource}:{resource:IAndroidWatchFile}){
    return(
        <Box>
            <Stack direction="column" spacing={2}>
                <span>{resource.client.path}</span>
                {/* <IFrameReactContainer content={createHtml(resource.content)} /> */}
                <IFrameReactContainer content={resource.content} htmlBuilder={marketHtmlUtils.createHtml} />
            </Stack>
        </Box>
    )
}

export function Scripts(){
    const [resources] = useProxyWatchUpdates(marketsDevScriptLib.marketDevLibData, "allMarketLibData.resources", marketsDevScriptLib.marketDevLibData.allMarketLibData?.resources || []);
    return(
        <Box>
            {
                marketsDevScriptLib.marketDevLibData.allMarketLibData?.resources?.map((resource, index)=>{
                    return(
                        <Script key={resource.content} resource={resource} />
                    )
                })
            }
        </Box>
    )
}