import { PropsWithChildren } from "react";
import { AppShell } from "./AppShell";
import { ContextWrapper, ContextWrapperProps } from "@fanfanlo";

//回头想想AppShell里的s要怎么用
// createTypedContext方法返回的context没想好怎么样跟s结合使用。

export function AppPageDataWrapperShell<T>({ children, contextValue, ContextProvider }: PropsWithChildren & ContextWrapperProps<T>) {
    return (
        <AppShell>
            <ContextWrapper contextValue={contextValue} ContextProvider={ContextProvider}>
                {children}
            </ContextWrapper>
        </AppShell>
    )
}