'use client'
import { getSocketConnectStatusEnumI18n, isValidWebSocketURL, SocketConnectStatusEnum, useProxyWatch, useTranslation } from "@fanfanlo";
import { Box, Button, Checkbox, FormControlLabel, Grid2, Stack, TextField } from "@mui/material";
import { marketsDevSocket } from "@src/markets/market-dev-socket";
import { useEffect, useState } from "react";
import { useMarketsDevSettingsContext } from "../data/context";

function URL() {
    const { t } = useTranslation('homepage/components/page/markets-dev-settings/content/content');
    const pageData = useMarketsDevSettingsContext()
    const [value, _setValue] = useState(pageData.socketSettingData.url)
    const [isWrong, setIsWrong] = useState(!isValidWebSocketURL(value))
    function setValue(v: string) {
        _setValue(v)
        pageData.socketSettingData.url = v
        if (!isValidWebSocketURL(v)) {
            setIsWrong(true)
        } else {
            setIsWrong(false)
        }
    }
    const s = t("DevSettings.socket.url.helperText", { 0: "socket", 1: "ws", 2: "wss", 3: "url" })
    return (
        <Box>
            <TextField fullWidth
                error={isWrong}
                label={t("DevSettings.socket.url.name")}
                value={value}
                placeholder={"ws://192.168.1.2:7001"}
                helperText={isWrong ? s : ""}
                variant="standard"
                onChange={(e) => setValue(e.target.value)}
            />
        </Box>
    )
}

function ConnectButton2() {
    const pageData = useMarketsDevSettingsContext()
    const [status] = useProxyWatch(marketsDevSocket.socketConnectInfo, 'status', marketsDevSocket.socketConnectInfo.status);
    const [buttonName, setButtonName] = useState('')
    const { t } = useTranslation('homepage/components/page/markets-dev-settings/content/content');
    const [url] = useProxyWatch(pageData.socketSettingData, 'url', pageData.socketSettingData.url);
    const [enabled, setEnabled] = useState(false)
    useEffect(() => {
        setEnabled((status === SocketConnectStatusEnum.disconnected || status === SocketConnectStatusEnum.connected) && isValidWebSocketURL(url))
    }, [status, url])
    useEffect(() => {
        async function updateButtonName() {
            const status = marketsDevSocket.socketConnectInfo.status
            let name = buttonName;

            switch (status) {
                case SocketConnectStatusEnum.connecting:
                    name = await getSocketConnectStatusEnumI18n(SocketConnectStatusEnum.connecting)
                    break;
                case SocketConnectStatusEnum.connected:
                    name = t("DevSettings.connectButton.disconnect")
                    break;
                case SocketConnectStatusEnum.disconnecting:
                    name = await getSocketConnectStatusEnumI18n(SocketConnectStatusEnum.disconnecting)
                    break;
                case SocketConnectStatusEnum.disconnected:
                    name = t("DevSettings.connectButton.connect")
                    break;
            }
            if(marketsDevSocket.socketConnectInfo.status !== status){
                return updateButtonName();
            }
            if(name !== buttonName){
                setButtonName(name)
            }
        }
        updateButtonName()
    }, [status])
    function onClick() {
        if (status === SocketConnectStatusEnum.disconnected) {
            marketsDevSocket.connect();
        } else {
            marketsDevSocket.disconnect();
        }
    }
    return (
        <Button
            variant="contained"
            disabled={!enabled}
            onClick={onClick}
        >
            {buttonName}
        </Button>
    )
}

function KeepConnectCheckbox() {
    const pageData = useMarketsDevSettingsContext()
    const [keepConnect] = useProxyWatch(pageData.socketSettingData, 'keepConnect', pageData.socketSettingData.keepConnect ?? false);
    const { t } = useTranslation('homepage/components/page/markets-dev-settings/content/content');
    return (
        
        <FormControlLabel control={<Checkbox
            checked={keepConnect}
            onChange={(e) => pageData.socketSettingData.keepConnect = e.target.checked}
        />}
            label={<span><span>{t("DevSettings.socket.keepConnect.name")}</span><span style={{fontSize:'0.8rem', color:'gray'}}>{t("DevSettings.socket.keepConnect.helperText")}</span></span>}>

        </FormControlLabel>
    )
}

function CheckboxButtonGroup() {
    return (
        <Grid2 container direction={"row"} spacing={2}>
            <Grid2 size={"grow"}>
                <KeepConnectCheckbox />
            </Grid2>
            <Grid2 size={"auto"}>
                <ConnectButton2 />
            </Grid2>
        </Grid2>
    )
}

export function DevSettings() {
    const pageData = useMarketsDevSettingsContext()
    useEffect(() => {
        if(pageData.socketSettingData.keepConnect){
            marketsDevSocket.connect();
        }
    }, [])
    return (
        <>
            <Box>
                <Stack direction={"column"} spacing={2}>
                    <URL />
                    <CheckboxButtonGroup />
                </Stack>
            </Box>
        </>
    )
}