import { AppbarContainer, AppPageDataWrapperShell } from "@app/ui";
import { autoWebViewJs } from "@droid/android";
import { Dispatcher, isValidWebSocketURL, useTranslation } from "@fanfanlo";
import { IScriptConf } from "@market/lib";
import { Box, Button } from "@mui/material";
import { MarketsDevSettingsContent } from "@src/components/page/markets-dev-settings/content/MarketsDevSettingsContent";
import { MarketsDevSettingsContextProvider } from "@src/components/page/markets-dev-settings/data/context";
import { IPageData, marketsDevSettingsLoadDefData } from "@src/components/page/markets-dev-settings/data/data.intf";
