import { Suspense, use } from "react";

export default function MarketsDevSettingsPage() {
    const { t } = useTranslation('homepage/pages/markets-dev-settings/content');
    return (
        <Suspense fallback={<div>{t("content.loadPageData")}</div>}>
            <Content pageData={marketsDevSettingsLoadDefData()} libInit={marketsDevScriptLib.init()} />
        </Suspense>
    )
}

function Content({ pageData, libInit }: { pageData: Promise<IPageData>, libInit: Promise<void> }) {

    const { t } = useTranslation('homepage/pages/markets-dev-settings/content');
    const data = use(pageData)
    const dispatcher = new Dispatcher()
    use(libInit)

    async function onClick() {
        const res = await getContacts();
        console.log('on click res=', res);
    }
    return (
        <AppPageDataWrapperShell
            contextValue={data}
            ContextProvider={MarketsDevSettingsContextProvider}
        >
            <AppbarContainer appbarProps={{ title: t("content.title"), back: true, backDispatcher: dispatcher }}>
                <Box>
                    <Button onClick={onClick}>script</Button>
                </Box>
                <MarketsDevSettingsContent />
            </AppbarContainer>
        </AppPageDataWrapperShell>
    )
}


