import { toProxy } from "@fanfanlo";
import { getMarketDevSocketSettingData } from "@src/markets/market-dev-socket-setting-data";

export interface ISocketSettingData{
    //如果点击开始链接了，那就保持链接，这样需要注意不在开发环境时会不停重连。
    keepConnect:boolean;
    url:string;
}

export interface IPageData{
    socketSettingData:ISocketSettingData;
}

async function createDefPageData():Promise<IPageData>{
    return {
        socketSettingData:await getMarketDevSocketSettingData()
    }
}

let dataImpl:IPageData|undefined
export async function marketsDevSettingsLoadDefData():Promise<IPageData>{
    if(dataImpl){
        return dataImpl;
    }
    let data:IPageData = await createDefPageData();
    dataImpl = toProxy(data);
    return dataImpl;
}